<template>
  <div>
    <transition name='show-zhezhao'>
      <div class="zhezhao" v-show="show_userinfo" @click='show_user'>
      </div>
    </transition>
    <transition name='slide-left'>
      <div class="left-side" v-show="show_userinfo">
        <div id="app" @touchmove.prevent>
          <ttt>
            <template slot="header">
              <h1>标题</h1>
            </template>
            <template slot="content">
              <p>可滑动</p>
            </template>
          </ttt>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import ttt from '@/components/header'
export default {
  components: {
    ttt
  },
  data: () => {
    return {
      show_zhezhao: false
    }
  },
  computed: {
    show_userinfo: function () {
      return this.$store.state.show_userinfo
    }
  },
  methods: {
    show_user () {
      this.$store.state.show_userinfo = !this.$store.state.show_userinfo
    }
  }
}
</script>
<style>
.zhezhao {
  position: fixed;
  height: 100%;
  width: 100%;
  background: #aaa;
  z-index: 1040;
  opacity: .5
}

.left-side {
  position: fixed;
  width: 400px;
  height: 100%;
  background: #fff;
  z-index: 1050;
  opacity: 1
}

@media only screen and (max-width: 420px) {
  .left-side {
    width: 300px;
  }
}


.info-header {
  height: 20%;
  background: #5bc0de;
  text-align: center;
  font-size: 52px;
}

.slide-left-enter-active {
  animation: slide-left-in .5s ease
}

.slide-left-leave-active {
  animation: slide-left-out .5s ease
}

@keyframes slide-left-in {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slide-left-out {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

.show-zhezhao-leave-active {
  animation: show-zhezhao-out .4s ease
}


@keyframes show-zhezhao-out {
  0% {}
  100% {}
}
</style>



