<template>
  <div>
    <div class='row maptab'>
      <div @click='enter(pos.xinxiao)' class="col-xs-2">
              <tag txt='新校'></tag>
            </div>
            <div @click='enter(pos.nanxiao)' class="col-xs-2">
              <tag txt='南校'></tag>
            </div>
            <div @click='enter(pos.benbu)' class="col-xs-2">
              <tag txt='本部'></tag>
            </div>
            <div @click='enter(pos.xiangya)' class="col-xs-2">
              <tag txt='湘雅'></tag>
            </div>
            <div @click='enter(pos.tiedao)' class="col-xs-2">
              <tag txt='铁道'></tag>
            </div>
              <button @click='create_new_event' class="btn btn-danger col-md-1">发布 </button>
    </div>
    <baidu-map class='bm' :center='position.center' @zoomend='synczoom' @moveend='sync' :scroll-wheel-zoom='scroll' :zoom='position.zoom'>
    <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_LEFT"></bm-navigation>
    <bm-marker v-if='centerlabel' 
    :icon="{url: './static/img/arrow.ico', size: {width: 64, height: 64}}" 
    :position="position.center" 
    :dragging='dragging' 
    animation="BMAP_ANIMATION_DROP" 
    @rightclick='get_pos'>
    <bm-info-window :show='tips'>拖动我选择地点，右键点我确认</bm-info-window>
    </bm-marker>
    <bm-marker :position='position.center' @click='show_event' :offset='{width: 0, height: 12.5}'><bm-info-window :show='true'>点击显示此处的事件，测试文章</bm-info-window></bm-marker>
    </baidu-map>
  </div>
</template>

<script>
  import tag from '@/components/tag'
  export default {
    data () {
      return {
        position: {// 点击定位一次后若鼠标拖动缩放   再点击相同地点  会出现错误
          center: {
            lng: 112.942658,
            lat: 28.164901
          },
          zoom: 15
        },
        dragging: true,
        scroll: true,
        centerlabel: false,
        tips: true,
        event_title: '',
        pos: {
          nanxiao: {
            lng: 112.942658,
            lat: 28.164901
          },
          xinxiao: {
            lng: 112.94644,
            lat: 28.154619
          },
          benbu: {
            lng: 112.935229,
            lat: 28.177018
          },
          xiangya: {
            lng: 112.949436, // 湘雅和铁道位置还没找!
            lat: 28.225955
          },
          tiedao: {
            lng: 112.999247,
            lat: 28.144036
          }
        }
      }
    },
    computed: {
      editing () {
        return this.$store.state.editing
      }
    },
    methods: {
      handler ({
        BMap,
        map
      }) {
        console.log(BMap, map)
        this.lng = 116.404
        this.lat = 39.915
      },
      sync (e) {
        this.position.center = e.target.getCenter()
      },
      synczoom (e) {
        this.position.zoom = e.target.getZoom()
      },
      enter (target) {
        setTimeout(() => {
          this.position.center = target
        }, 100)
        setTimeout(() => {
          this.position.zoom = 17.9
        }, 100)
        setTimeout(() => {
          this.position.zoom = 18
        }, 100)
      },
      create_new_event () {
        if (!this.$store.state.haslogin) {
          this.$swal({
            type: 'warning',
            text: '请先登录再进行操作'
          })
        } else
        if (!this.editing && this.centerlabel === false) {
          this.$swal.queue([{
            progressSteps: ['1', '2'],
            title: '第一步',
            text: '请输入事件标题',
            confirmButtonText: '下一步',
            input: 'text',
            preConfirm: (title) => {
              return new Promise((resolve, reject) => {
                if (title === '') {
                  reject('标题不能为空！')
                } else {
                  resolve() // 默认参数有 input
                }
              })
            }
          }, {
            progressSteps: ['1', '2'],
            title: '第二步',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '去选取'
          }]).then((data) => {
            console.log(data)
            this.centerlabel = true
            this.event_title = data[0]
            setTimeout(() => {
              this.tips = false
            }, 3000)
          }).catch(this.$swal.noop)
        } else if (this.centerlabel === true) { // 当开始选择地点又还没右键确定的时候如果再点击发布
          this.$swal({
            type: 'warning',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '确定'
          })
        } else {
          this.$router.push('edit')
        }
      },
      get_pos (event) {
        console.log(event.point)
        this.$router.push({
          name: 'edit', // 只能用name来传params，如果用path数据就传不过去  也不知道为什么。
          params: {
            location: event.point,
            title: this.event_title
          }
        })
        this.$store.commit('startedit')
        this.centerlabel = false
        this.tips = true
      },
      show_event (event) {
        this.$swal({
          width: 1500,
          confirmButtonText: '看完了',
          allowOutsideClick: false,
          html: '<h2>体测</h2> <p>早上六点半醒来，上次这个点起床的时候，大清还在。<br><br /> 八点竟然和舍友一起去八食堂吃早饭，这个场景，简直活在梦里。不过头还是很晕，走楼梯都感觉晃来晃去的。<br></p> <p>回来看了一会奇才大战凯尔特人，可能是看多了勇士的球，总觉得缺了点什么。女票找了熬夜有害论的证据来强制要求我早睡。我也有点怂了。这不我现在定下规矩，十二点开始写blog,写完就睡。</p> <p>十点的时候就感觉头晕，都不知道下午能不能跑完全程，然后就又上去睡觉了。这一睡就是下午两点半才起，不过这一觉真的是睡的我神采飞扬眉吐气定神闲。就很开心的吃了半碗皮蛋瘦肉粥就去体测了。</p> <h3>立定跳远</h3> <!--- more ---> <iframe frameborder="no" border="0" marginwidth="0" marginheight="0" width=330 height=86 src="//music.163.com/outchain/player?type=2&id=5345923&auto=1&height=66"></iframe> <p>第一个项目，差点来不及，尼玛都要到鸟巢了发现校园卡没带，顶着烈日又跑回去拿，心疼自己像个ZZ。不过跳的还行，2.50M。大部分男生也差不多这个水平，除了一个老崔，这好家伙快三米了都。</p> <h3>引体向上</h3> <p>这个引体向上老师很严格，还要求手腕必须伸直，还好我一直以来都是有按标准来的。不过就是心疼了我的几个同学，其中一个说练了半个月，已经有成效了，没想到练得是没伸直手臂的版本，又遇上这么个老师，结果他做的时候，老师计数:“1,2,2,2,3。“很无奈只能放弃了。然后是我们班上的一个大神，看着瘦小，肌肉比谁都发达，听他舍友说他肌肉松弛状态下都是各种棱角，体重又轻。然后就开始了他的表演，一连做了二十几个标准的不费劲。老师都很惊讶，叫他不要做了，已经满分了。然后就是我了，我这个大水比，太久没练了，做了7个就不行了。</p> <h2>50米</h2> <p>其实很不想跑50米，今天本来就有点头晕，就感觉有东西一直跳，然后也没用全力跑，测了个7.1S，应该是表的误差，我估计实际成绩应该是7.3S左右。</p> <h2>1000米</h2> <p>起跑前还有个老师给我们做指导，首先就给我们提了个要求，可以跑得慢，但跑完必须要能活着回去。然后给我们讲了一大堆跑路哦不对跑步的要领。最后总结就是：慢点跑。别说还真挺管用，今天跑完1000米，虽然成绩是历史最差，但是状态是历史最好了，带病上场，跑完还能活蹦乱跳的。我从初中到大学的1000米成绩变化趋势是这样的：<br /> <img src="http://blog.edwarddd.cn/hexo-blog/img/1000m.png" alt="" /></p> <h2>下馆子</h2> <p>今天是老崔的生日啊，哥几个肯定是要聚在一起吹吹比的，他为了和我们一起下馆子，都没和他女票一起过，东北真性情老爷们。</p> <p>体测完他和去年一样吐了一地胆汁，果然还是不能跑太猛，然后被搀扶着回来，休息一会后我们就出发了。去了一家以前去过的湘菜馆，味道很不错，然后就开始吹牛逼，一群男的吹起来，那可是天文地理无所不谈，从运动场聊到秦始皇，从最近的黑客事件聊到FBI。吹到饭店打烊才慢慢离开。路上经过老崔曾经说过的世界上最好喝的奶茶店，可惜人太挤，就在隔壁一家没人的奶茶店买了今天的污点，最难喝的奶茶店。回去也不愿意走寻常路，还得从邻居学校的学生公寓穿行而回。那一路上继续谈笑风生，纵观人生百态，我很多地方还是too young too simple。回到宿舍大家还一起吃了他的女友蛋糕，哈哈，我心里在暗喜上次我女票上次给我的黑森林蛋糕不知道比这个好吃到哪里去了。</p> <h2>关于流水账</h2> <p>写博客的时候感觉有点力不从心，像小学生作文，还是要多读点书，肚子里有点墨水，写出来的才不至于这么naive把。<br><br /> 突然发现这篇文章好像膜的有点过分了，希望不会查水表~~~~(&gt;_&lt;)~~~~</p>'
        })
      }
    },
    components: {
      tag
    }
  }
</script>

<style>
  .bm {
    position: absolute;
    top: 100px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2)
  }
  .maptab {
    position: fixed;
    top: 61px;
    left: 0;
    right: 0;
  }
  .col-xs-2 {
    text-align: center
  }
</style>



